<template>
    <div class="tData">
        <div class="header">
            <BreadCrumb />
        </div>
        <div class="content">
            <el-card class="box-card">
                <div style="width:100%;height:50px;margin:10px 0">
                    <el-button type="primary" @click="handleAdd()"></el-button>
                    <!-- <el-button type="warning" @click="handlePrint()"></el-button> -->
                    <el-button type="danger" @click="handleExport()"></el-button>
                </div>
                <table-pagination
                        id="TimeDataTable"
                        @handleSizeChange="handleSizeChange"
                        @handleCurrentChange="handleCurrentChange "
                        :data="tableData"
                        :columns="columns"
                        :pagination="pagination"
                ></table-pagination>
            </el-card>


            <el-dialog title="" :visible.sync="dialogModifyFormVisible" @close="closeModify('modifyForm')">
              <el-form :model="modifyForm" class="addArea" :rules="rules" ref="modifyForm">
                <el-form-item label=":" :label-width="formLabelWidth" prop="name" style="width:50%;float:left">
                  <el-input v-model="modifyForm.name" autocomplete="off" placeholder=""></el-input>
                </el-form-item>

                <el-form-item label=":" :label-width="formLabelWidth"  prop="age" style="width:50%;float:left">
                    <el-input v-model.number="modifyForm.age" auto-complete="off" placeholder=""></el-input>
                </el-form-item>

                <el-form-item label=":" :label-width="formLabelWidth" prop="sex" style="width:50%;float:left">
                  <el-select v-model="modifyForm.sex" placeholder="" class="sexArea">
                    <el-option label="" value="man"></el-option>
                    <el-option label="" value="woman"></el-option>
                  </el-select>
                </el-form-item>

                <el-form-item label=":" :label-width="formLabelWidth" prop="address" style="width:50%;float:left">
                  <el-input v-model="modifyForm.address" auto-complete="off" placeholder=""></el-input>
                </el-form-item>

                <el-form-item label=":" :label-width="formLabelWidth" prop="dowhat" style="width:50%;float:left">
                  <el-input v-model="modifyForm.dowhat" auto-complete="off" placeholder=""></el-input>
                </el-form-item>


                <el-form-item label=":" :label-width="formLabelWidth" prop="favourite" style="width:50%;float:left">
                    <el-input v-model="modifyForm.favourite" auto-complete="off" placeholder=""></el-input>
                </el-form-item>

                <el-form-item label=":" :label-width="formLabelWidth" prop="explain" style="width:100%;float:left">
                  <el-input v-model="modifyForm.explain" auto-complete="off" type="textarea" :rows="6" resize="none" placeholder=""></el-input>
                </el-form-item>
              </el-form>
              <div slot="footer" class="dialog-footer">
                <el-button @click="dialogFormVisible = false"> </el-button>
                <el-button type="primary" @click="modifySure()"> </el-button>
              </div>
            </el-dialog>

            <el-dialog title="" :visible.sync="dialogAddFormVisible" @close="closeModify('addForm')">
              <el-form :model="addForm" class="addArea" :rules="rules" ref="addForm">
                <el-form-item label=":" :label-width="formLabelWidth" prop="name" style="width:50%;float:left">
                  <el-input v-model="addForm.name" autocomplete="off" placeholder=""></el-input>
                </el-form-item>

                <el-form-item label=":" :label-width="formLabelWidth"  prop="age" style="width:50%;float:left">
                    <el-input v-model.number="addForm.age" auto-complete="off" placeholder=""></el-input>
                </el-form-item>

                <el-form-item label=":" :label-width="formLabelWidth" prop="sex" style="width:50%;float:left">
                  <el-select v-model="addForm.sex" placeholder="" class="sexArea">
                    <el-option label="" value="man"></el-option>
                    <el-option label="" value="woman"></el-option>
                  </el-select>
                </el-form-item>

                <el-form-item label=":" :label-width="formLabelWidth" prop="address" style="width:50%;float:left">
                  <el-input v-model="addForm.address" auto-complete="off" placeholder=""></el-input>
                </el-form-item>

                <el-form-item label=":" :label-width="formLabelWidth" prop="dowhat" style="width:50%;float:left">
                  <el-input v-model="addForm.dowhat" auto-complete="off" placeholder=""></el-input>
                </el-form-item>


                <el-form-item label=":" :label-width="formLabelWidth" prop="favourite" style="width:50%;float:left">
                    <el-input v-model="addForm.favourite" auto-complete="off" placeholder=""></el-input>
                </el-form-item>

                <el-form-item label=":" :label-width="formLabelWidth" prop="explain" style="width:100%;float:left">
                  <el-input v-model="addForm.explain" auto-complete="off" type="textarea" :rows="6" resize="none" placeholder=""></el-input>
                </el-form-item>
              </el-form>
              <div slot="footer" class="dialog-footer">
                <el-button @click="dialogAddFormVisible = false"> </el-button>
                <el-button type="primary" @click="addSure()"> </el-button>
              </div>
            </el-dialog>


          <!--  -->
          <el-dialog title="" :visible.sync="addpicVisible" class="addPicArea" @close="closePicAdd()">
            <el-form :model="addpicform">
              <el-form-item label="" :label-width="formLabelWidth">
                <el-input v-model="addpicform.url" auto-complete="off"></el-input>
              </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
              <el-button @click="addpicVisible = false"> </el-button>
              <el-button type="primary" @click="addpicSure()"> </el-button>
            </div>
          </el-dialog>
        </div>
    </div>
</template>

<script>
    import TablePagination from "@/components/yTablePagination"
    import BreadCrumb from "@/components/yBreadCrumb"
    import FileSaver from 'file-saver'
    import XLSX from 'xlsx'
    export default {
        name:"TimeData",
        components:{
            TablePagination,
            BreadCrumb
        },
        data() {
            return {
                columns:[
                    { prop : 'name' , label: '',width:'100px'},
                    { prop : 'age', label: '',width:'100px'},
                    { prop : 'sex', label: '',width:'100px',
                        formatter: (row) => {
                            if (row.sex === "man") {
                                return "";
                            } else if (row.sex === "woman") {
                                return "";
                            } else {
                                return "";
                            }
                        }
                    },
                    { prop : 'address', label: '',width:'100px'},
                    { prop : 'dowhat', label: '',width:'100px'},
                    { prop : 'favourite', label: '',width:'150px',
                        formatter: (row) => {
                            return `<span style="white-space: nowrap;color: #1e90ff;">${row.favourite}</span>`
                        }
                    },
                    { prop : '', label: '',
                        operations:[{
                                label: '',
                                type: "primary",
                                method: (row) => {
                                    this.handleDetails(row)
                                }
                            },
                            {
                                label: '',
                                type: "success",
                                method: (row) => {
                                    this.handleModify(row)
                                    }
                            },{
                                label: '',
                                type: "danger",
                                method: (row) => {
                                    this.handleDelete(row)}
                            },{
                                label: '',
                                type: "warning",
                                method: (row) => {
                                    this.handleAddPic(row)
                                    }
                            }
                    ]},
                ],
                      // 
                rules: {
                  name: [
                    { required: true, message: "", trigger: "blur" }
                  ],
                  age: [
                    { required: true, message: "", trigger: "blur" },
                  ],
                  sex: [
                    { required: true, message: "", trigger: "change" },
                    { required: true, message: "", trigger: "blur" }
                  ],
                  address: [
                    { required: true, message: "", trigger: "blur" }
                  ],
                  dowhat: [
                    { required: true, message: "", trigger: "blur" }
                  ],
                  favourite: [
                    { required: true, message: "", trigger: "blur" }
                  ],
                  explain: [
                    { required: true, message: "", trigger: "blur" }
                  ]
                },
                tableData: [],
                pagination: {
                    total:0,
                    currentPage: 1,
                    pageSize: 2,
                    pageSizes:[2, 4, 6]
                },
                dialogModifyFormVisible: false,
                dialogAddFormVisible:false,
                addpicVisible:false,
                modifyForm: {
                    name: '',
                    age: '',
                    sex: '',
                    address: '',
                    dowhat: '',
                    favourite: '',
                    explain: ''
                },
                addForm: {
                    name: '',
                    age: '',
                    sex: '',
                    address: '',
                    dowhat: '',
                    favourite: '',
                    explain: ''
                },
                formLabelWidth: '80px',
                modifyId:'',
                addpicId: "",
                addpicform: {
                  url: ""
                },
            }
        },
        created(){
            this.getData()
        },
        methods:{
            getData(){
                this.$ajax.get('/api/Hero/').then(res=>{
                    this.tableData=res.data;
                    this.pagination.total=res.data.length;
                })
            },
            //
            handleDetails(row) {
                var id = row['_id'];
                this.$router.push(`/TimeData/${id}`);
            },
            handleSizeChange (size) {
                this.pagination.pageSize=size;
                // console.log(' this.limit:', size)
            },
            // 
            handleCurrentChange (index) {
                this.pagination.currentPage=index;
                // console.log(' this.page:', index)
            },
                // dialog
            closeModify: function(formName) {
              this.$refs[formName].resetFields();
            },

            handleModify(row) {
                this.dialogModifyFormVisible=true;
                this.modifyForm = Object.assign({}, row);
                this.modifyId = row['_id'];
            },
            handleAdd(){
                this.dialogAddFormVisible=true;
            },
            // handlePrint(){
            //     // this.dialogAddFormVisible=true;
            // },
            handleExport(){
                // this.dialogAddFormVisible=true;
                var wb = XLSX.utils.table_to_book(document.querySelector('#TimeDataTable'))
                /* get binary string as output */
                var wbout = XLSX.write(wb, { bookType: 'xlsx', bookSST: true, type: 'array' })
                try {
                    FileSaver.saveAs(new Blob([wbout], { type: 'application/octet-stream' }), 'TimeDataTable.xlsx')
                } catch (e) { if (typeof console !== 'undefined') console.log(e, wbout) }
                return wbout
            },
            addSure(){
                const _this=this;
                this.$confirm('?', '', {
                    type: 'warning'
                }).then(() => {
                    this.$ajax.post(`/api/Hero/addHero`,this.addForm).
                    then(res=>{
                        if(res.status==200){
                          this.dialogAddFormVisible=false;
                          this.$message({
                              message: "",
                              type: "success",
                              onClose: function() {
                                _this.getData();
                              }
                          });
                        }else{
                          this.$message({
                              message: "",
                              type: "error",
                              onClose: function() {
                                _this.getData()
                              }
                          });
                        }
                    })
                }).catch((err) => {
                    console.log(err)
                });
            },


            modifySure() {
               const _this=this;
                this.$confirm('?', '', {
                    type: 'warning'
                }).then(() => {
                    this.$ajax.post(`/api/Hero/putHero/${this.modifyId}`,this.modifyForm).
                    then(res=>{
                        if(res.status==200){
                          this.dialogModifyFormVisible=false;
                          this.$message({
                              message: "",
                              type: "success",
                              onClose: function() {
                                _this.getData();
                              }
                          });
                        }else{
                          this.$message({
                              message: "",
                              type: "error",
                              onClose: function() {
                                _this.getData()
                              }
                          });
                        }
                    })
                }).catch((err) => {
                    console.log(err)
                });
              },
            //
            handleDelete(row) {
                // console.log(id);
                var id = row['_id'];
               const _this=this;
                this.$confirm(', ?', '', {
                    type: 'warning'
                }).then(() => {
                    this.$ajax.delete(`/api/Hero/delHero/${id}`).
                    then(res=>{
                        // console.log(res)
                    if (res.data.status == "success") {
                        this.$message({
                            message: "",
                            type: "success",
                            onClose: function() {
                               _this.getData()
                            }
                        });
                        } else {
                        this.$message({
                            message: "",
                            type: "error",
                            onClose: function() {
                               _this.getData()
                            }
                        });
                        }
                    })
                }).catch((err) => {
                    console.log(err)
                });

            },
                // 
            closePicAdd: function() {
              this.addpicform.url = "";
            },
            handleAddPic(row){
                this.addpicVisible=true;
                this.addpicId = row['_id'];
            },
            addpicSure() {
                const _this=this;
                if (!this.addpicform.url.trim()) {
                  this.$notify.error({
                    title: "",
                    message: ""
                  });
                  return;
                }

                var addObj = {
                  url: this.addpicform.url
                };
                this.$confirm('?', '', {
                    type: 'warning'
                }).then(() => {
                    this.$ajax.put(`/api/Hero/addpic/${this.addpicId}`,addObj).
                    then(res=>{
                        if(res.status==200){
                          this.addpicVisible=false;
                          this.$notify({
                            title: "",
                            message: "",
                            type: "success",
                            onClose: function() {
                              _this.getData()
                            }
                          });
                        }else{
                            this.$notify({
                              title: "",
                              message: "",
                              type: "error",
                              onClose: function() {
                                _this.getData()
                              }
                            });
                        }
                    })
                }).catch((err) => {
                    console.log(err)
                });





            }
        }
    }
</script>

<style scoped>
.tData{
    width: 100%;
    height: 100%;
    overflow:hidden;
}


</style>



